import React, { useEffect, useState } from "react";
import { Parallax } from "react-parallax";
import galaxy from "../images/galaxy.jpeg";
import { AppBar, Box, FormControlLabel, Switch } from "@mui/material";
import Color from "../config/Color";

const Home = () => {
  const [name, setName] = useState("");
  const [theme, setTheme] = useState("dark");
  const [themeBool, setThemeBool] = useState(true);

  const getData = () => {
    fetch("http://localhost:8080")
      .then((res) => res.json())
      .then((data) => {
        console.log(" data ", data);
        setName(data.name);
      });
  };

  useEffect(() => {
    getData();
    localStorage.setItem("theme", theme);
    document.body.className = theme;
  }, [theme]);

  const insideStyles = {
    // background: "white",
    padding: 20,
    position: "absolute",
    top: "20%",
    left: "50%",
    transform: "translate(-50%,-50%)",
    color: "white",
  };

  const toggleTheme = () => {
    if (theme === "light") {
      setTheme("dark");
      setThemeBool(true);
    } else {
      setTheme("light");
      setThemeBool(false);
    }
  };

  const image1 =
    "https://images.unsplash.com/photo-1498092651296-641e88c3b057?auto=format&fit=crop&w=1778&q=60&ixid=dW5zcGxhc2guY29tOzs7Ozs%3D";
  const image2 =
    "https://img00.deviantart.net/2bd0/i/2009/276/c/9/magic_forrest_wallpaper_by_goergen.jpg";

  return (
    <>
      <AppBar
        style={{ backgroundColor: themeBool ? Color.black : Color.white }}
      >
        <FormControlLabel
          control={
            <Switch
              Switch={themeBool}
              onChange={toggleTheme}
              style={{ color: themeBool ? Color.white : Color.black }}
            />
          }
          style={{
            backgroundColor: themeBool ? "black" : "white",
            color: themeBool ? Color.white : Color.black,
            justifyContent: "end"
          }}
          label={themeBool ? "Dark" : "Light"}
        />
      </AppBar>
      <Parallax bgImage={image1} strength={500}>
        <div style={{ height: 800 }}>
          <h1 style={insideStyles}>Hello {name}</h1>
        </div>
      </Parallax>
      <Parallax bgImage={image2} strength={-100}>
        <div style={{ height: 800 }}>
          <div style={insideStyles}>Reverse direction</div>
        </div>
      </Parallax>
      <Box sx={{ height: 5000 }} />
      <footer>&copy; Copy Right</footer>
    </>
  );
};

export default Home;
